'use client'

import { useEffect, useState } from 'react'
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { 
  MapPin, 
  Users, 
  Package, 
  TrendingUp, 
  Filter,
  Search,
  ChevronRight,
  Home,
  Scale,
  User,
  Clock,
  Calendar,
  Eye
} from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Progress } from '@/components/ui/progress'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Separator } from '@/components/ui/separator'
import { Skeleton } from '@/components/ui/skeleton'

type Farmer = {
  id: number
  first_name: string
  last_name: string
  phone_number?: string
  total_farms?: number
}

type Field = {
  id: number
  field_name: string
  location: string
  total_area: number
  cultivatable_area: number
  ownership_type_display: string
  farmer_name: string
  crops_grown?: string[]
  soil_type?: string
  irrigation_type?: string
  created_at?: string
  last_visited?: string
}

export function FarmsList() {
  const [farmers, setFarmers] = useState<Farmer[]>([])
  const [selectedFarmer, setSelectedFarmer] = useState<string>('all')
  const [fields, setFields] = useState<Field[]>([])
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')

  // Stats calculation
  const totalFarms = fields.length
  const totalArea = fields.reduce((sum, field) => sum + field.total_area, 0)
  const totalCultivableArea = fields.reduce((sum, field) => sum + field.cultivatable_area, 0)
  const cultivablePercentage = totalArea > 0 ? (totalCultivableArea / totalArea) * 100 : 0
  const uniqueFarmers = [...new Set(fields.map(f => f.farmer_name))].length

  // âœ… Fetch all farmers
  useEffect(() => {
    const fetchFarmers = async () => {
      const token = localStorage.getItem('authToken')
      if (!token) return

      try {
        const res = await fetch(
          'https://rda.ngrok.app/api/subadmin/assigned-farmers/',
          {
            headers: {
              Authorization: `Bearer ${token}`,
              'ngrok-skip-browser-warning': 'true',
            },
          }
        )
        const data = await res.json()
        setFarmers(data.assigned_farmers || [])
      } catch (error) {
        console.error('Error fetching farmers:', error)
      }
    }

    fetchFarmers()
  }, [])

  // âœ… Fetch ALL fields (default view)
  const fetchAllFields = async () => {
    setLoading(true)
    const token = localStorage.getItem('authToken')

    try {
      const res = await fetch(
        'https://rda.ngrok.app/api/fields/',
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        }
      )
      const data = await res.json()
      console.log('Fetched all fields:', data)
      setFields(data.data || [])
    } catch (error) {
      console.error('Error fetching fields:', error)
      setFields([])
    } finally {
      setLoading(false)
    }
  }
  

  // âœ… Fetch fields by farmer
  const fetchFieldsByFarmer = async (farmerId: string) => {
    setLoading(true)
    const token = localStorage.getItem('authToken')

    try {
      const res = await fetch(
        `https://rda.ngrok.app/api/fields/farmer/${farmerId}/`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'ngrok-skip-browser-warning': 'true',
          },
        }
      )
      const data = await res.json()
      setFields(data.data || [])
    } catch (error) {
      console.error('Error fetching farmer fields:', error)
      setFields([])
    } finally {
      setLoading(false)
    }
  }

  // âœ… Load all fields on first render
  useEffect(() => {
    fetchAllFields()
  }, [])

  // Filter fields based on search query
  const filteredFields = fields.filter(field =>
    field.field_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    field.location.toLowerCase().includes(searchQuery.toLowerCase()) ||
    field.farmer_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    (field.crops_grown?.some(crop => crop.toLowerCase().includes(searchQuery.toLowerCase())))
  )

  const getOwnershipColor = (type: string) => {
    const colors: Record<string, string> = {
      'owned': 'bg-green-100 text-green-800',
      'leased': 'bg-blue-100 text-blue-800',
      'rented': 'bg-amber-100 text-amber-800',
      'custom': 'bg-purple-100 text-purple-800',
    }
    return colors[type.toLowerCase()] || 'bg-gray-100 text-gray-800'
  }

  const getSoilColor = (type?: string) => {
    if (!type) return 'bg-gray-100'
    const colors: Record<string, string> = {
      'loamy': 'bg-yellow-100',
      'clay': 'bg-red-100',
      'sandy': 'bg-orange-100',
      'silt': 'bg-blue-100',
      'peat': 'bg-green-100',
    }
    return colors[type.toLowerCase()] || 'bg-gray-100'
  }

  return (
    <div className="space-y-8">
      {/* Header Section */}
      <div className="flex flex-col lg:flex-row justify-between items-start lg:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Farms Management</h1>
          <p className="text-muted-foreground mt-2">
            Monitor and manage all farm properties and their details
          </p>
        </div>
        <div className="flex items-center gap-3">
          <Button variant="outline" size="sm">
            <Package className="h-4 w-4 mr-2" />
            Export Data
          </Button>
        </div>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card className="border-l-4 border-l-primary">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Total Farms</p>
                <h3 className="text-2xl font-bold mt-2">{totalFarms}</h3>
              </div>
              <div className="h-12 w-12 rounded-full bg-primary/10 flex items-center justify-center">
                <Home className="h-6 w-6 text-primary" />
              </div>
            </div>
            <p className="text-xs text-muted-foreground mt-3">
              Across all farmers
            </p>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-green-500">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Total Area</p>
                <h3 className="text-2xl font-bold mt-2">{totalArea.toFixed(2)} acres</h3>
              </div>
              <div className="h-12 w-12 rounded-full bg-green-100 flex items-center justify-center">
                <Scale className="h-6 w-6 text-green-600" />
              </div>
            </div>
            <div className="mt-3">
              <div className="flex justify-between text-xs mb-1">
                <span>Cultivable Area</span>
                <span>{cultivablePercentage.toFixed(1)}%</span>
              </div>
              <Progress value={cultivablePercentage} className="h-2" />
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-blue-500">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Active Farmers</p>
                <h3 className="text-2xl font-bold mt-2">{uniqueFarmers}</h3>
              </div>
              <div className="h-12 w-12 rounded-full bg-blue-100 flex items-center justify-center">
                <Users className="h-6 w-6 text-blue-600" />
              </div>
            </div>
            <p className="text-xs text-muted-foreground mt-3">
              With registered farms
            </p>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-amber-500">
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-muted-foreground">Cultivable Land</p>
                <h3 className="text-2xl font-bold mt-2">{totalCultivableArea.toFixed(2)} acres</h3>
              </div>
              <div className="h-12 w-12 rounded-full bg-amber-100 flex items-center justify-center">
                <TrendingUp className="h-6 w-6 text-amber-600" />
              </div>
            </div>
            <p className="text-xs text-muted-foreground mt-3">
              Available for cultivation
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Filters and Controls */}
      <Card>
        <CardContent className="p-6">
          <div className="flex flex-col lg:flex-row gap-4 items-center">
            <div className="flex-1 w-full lg:w-auto">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                <Input
                  placeholder="Search farms by name, location, crop, or farmer..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-10 w-full lg:w-[400px]"
                />
              </div>
            </div>

            <div className="flex flex-wrap items-center gap-3">
              <div className="flex items-center gap-2">
                <Filter className="h-4 w-4 text-muted-foreground" />
                <Select
                  value={selectedFarmer}
                  onValueChange={(value) => {
                    setSelectedFarmer(value)
                    value === 'all'
                      ? fetchAllFields()
                      : fetchFieldsByFarmer(value)
                  }}
                >
                  <SelectTrigger className="w-[200px]">
                    <SelectValue placeholder="Select Farmer" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">
                      <div className="flex items-center gap-2">
                        <Users className="h-4 w-4" />
                        All Farmers
                      </div>
                    </SelectItem>
                    {farmers.map((farmer) => (
                      <SelectItem
                        key={farmer.id}
                        value={farmer.id.toString()}
                      >
                        <div className="flex items-center gap-2">
                          <User className="h-4 w-4" />
                          {farmer.first_name} {farmer.last_name}
                        </div>
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="flex border rounded-lg">
                <Button
                  variant={viewMode === 'grid' ? 'default' : 'ghost'}
                  size="sm"
                  className="rounded-r-none"
                  onClick={() => setViewMode('grid')}
                >
                  Grid
                </Button>
                <Button
                  variant={viewMode === 'list' ? 'default' : 'ghost'}
                  size="sm"
                  className="rounded-l-none"
                  onClick={() => setViewMode('list')}
                >
                  List
                </Button>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Results Section */}
      <div>
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold">
            {selectedFarmer === 'all' ? 'All Farms' : 'Selected Farmer Farms'}
            <span className="text-muted-foreground font-normal ml-2">
              ({filteredFields.length} {filteredFields.length === 1 ? 'farm' : 'farms'})
            </span>
          </h2>
          <p className="text-sm text-muted-foreground">
            Sorted by latest
          </p>
        </div>

        {loading ? (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {[1, 2, 3, 4].map((i) => (
              <Card key={i}>
                <CardHeader>
                  <Skeleton className="h-6 w-3/4" />
                  <Skeleton className="h-4 w-1/2 mt-2" />
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-2 gap-4">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-full" />
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        ) : filteredFields.length === 0 ? (
          <Card>
            <CardContent className="py-12 text-center">
              <Home className="h-12 w-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-700 mb-2">
                No farms found
              </h3>
              <p className="text-gray-500 max-w-sm mx-auto">
                {searchQuery 
                  ? "No farms match your search criteria. Try different keywords."
                  : "No farms are registered yet. Start by adding a new farm."
                }
              </p>
              {!searchQuery && (
                <Button className="mt-4">
                  <Home className="h-4 w-4 mr-2" />
                  Add First Farm
                </Button>
              )}
            </CardContent>
          </Card>
        ) : viewMode === 'grid' ? (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {filteredFields.map((field) => (
              <Card key={field.id} className="group hover:shadow-lg transition-shadow duration-300">
                <CardHeader className="pb-3">
                  <div className="flex justify-between items-start">
                    <div className="flex-1 min-w-0">
                      <CardTitle className="text-xl truncate group-hover:text-primary transition-colors">
                        {field.field_name}
                      </CardTitle>
                      <CardDescription className="flex items-center gap-1 mt-1">
                        <MapPin size={14} className="flex-shrink-0" />
                        <span className="truncate">{field.location}</span>
                      </CardDescription>
                    </div>
                    <Badge className={getOwnershipColor(field.ownership_type_display)}>
                      {field.ownership_type_display}
                    </Badge>
                  </div>
                </CardHeader>

                <CardContent>
                  <div className="space-y-4">
                    {/* Farmer Info */}
                    <div className="flex items-center gap-2 p-3 bg-gray-50 rounded-lg">
                      <User className="h-4 w-4 text-gray-500" />
                      <div>
                        <p className="text-sm font-medium">{field.farmer_name}</p>
                        <p className="text-xs text-muted-foreground">Field Owner</p>
                      </div>
                    </div>

                    {/* Area Stats */}
                    <div className="grid grid-cols-2 gap-4">
                      <div className="space-y-1">
                        <p className="text-xs text-muted-foreground">Total Area</p>
                        <div className="flex items-baseline gap-1">
                          <p className="text-2xl font-bold text-gray-900">{field.total_area}</p>
                          <span className="text-sm text-muted-foreground">acres</span>
                        </div>
                      </div>
                      <div className="space-y-1">
                        <p className="text-xs text-muted-foreground">Cultivable Area</p>
                        <div className="flex items-baseline gap-1">
                          <p className="text-2xl font-bold text-green-600">{field.cultivatable_area}</p>
                          <span className="text-sm text-muted-foreground">acres</span>
                        </div>
                      </div>
                    </div>

                    {/* Utilization Progress */}
                    <div>
                      <div className="flex justify-between text-xs mb-1">
                        <span>Land Utilization</span>
                        <span>
                          {field.total_area > 0 
                            ? ((field.cultivatable_area / field.total_area) * 100).toFixed(1)
                            : '0'}%
                        </span>
                      </div>
                      <Progress 
                        value={field.total_area > 0 ? (field.cultivatable_area / field.total_area) * 100 : 0} 
                        className="h-2" 
                      />
                    </div>

                    {/* Additional Info */}
                    {field.crops_grown && field.crops_grown.length > 0 && (
                      <div className="flex flex-wrap gap-2">
                        {field.crops_grown.map((crop, index) => (
                          <Badge key={index} variant="outline" className="text-xs">
                            {crop}
                          </Badge>
                        ))}
                      </div>
                    )}
                  </div>
                </CardContent>

                <CardFooter className="border-t pt-4 flex justify-between">
                  <div className="flex items-center gap-4 text-sm text-muted-foreground">
                    {field.last_visited && (
                      <div className="flex items-center gap-1">
                        <Clock className="h-3 w-3" />
                        <span>Visited {new Date(field.last_visited).toLocaleDateString()}</span>
                      </div>
                    )}
                  </div>
                  <Button variant="ghost" size="sm" className="gap-1">
                    View Details
                    <ChevronRight className="h-4 w-4" />
                  </Button>
                </CardFooter>
              </Card>
            ))}
          </div>
        ) : (
          /* List View */
          <Card>
            <div className="divide-y">
              {filteredFields.map((field) => (
                <div key={field.id} className="p-6 hover:bg-gray-50 transition-colors group">
                  <div className="flex items-center justify-between">
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-3 mb-2">
                        <h3 className="text-lg font-semibold group-hover:text-primary transition-colors">
                          {field.field_name}
                        </h3>
                        <Badge className={getOwnershipColor(field.ownership_type_display)}>
                          {field.ownership_type_display}
                        </Badge>
                      </div>
                      <p className="text-sm text-muted-foreground flex items-center gap-1 mb-3">
                        <MapPin size={14} />
                        {field.location}
                      </p>
                      <div className="flex items-center gap-6">
                        <div className="flex items-center gap-2">
                          <User className="h-4 w-4 text-gray-500" />
                          <span className="text-sm">{field.farmer_name}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <Scale className="h-4 w-4 text-gray-500" />
                          <span className="text-sm">{field.total_area} acres total</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <TrendingUp className="h-4 w-4 text-green-500" />
                          <span className="text-sm text-green-600">{field.cultivatable_area} acres cultivable</span>
                        </div>
                      </div>
                    </div>
                    <Button variant="ghost" size="sm" className="ml-4">
                      <Eye className="h-4 w-4 mr-2" />
                      View
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          </Card>
        )}
      </div>
    </div>
  )
}